<template>
  <div class="searchs">
    <div class="search">搜索</div>
    <van-search placeholder="请输入搜索关键词" v-model="val" @input="inpname(val)" />
    <div class="box">
      <div class="lovebook" v-for="(item,index) in seachbooks.list" :key="index" >
        <div class="fl imgs" @click="jump" >
          <img
            :src="item.cover"
            alt="暂时无图片"
          />
        </div>
        <div class="fl books clearfixed" @click="jump({name:'showpage', query:{seaurl:item.url,num:item.num,tag:item.tag}})">
          <span class="titlt onetext">{{item.name}}</span>
          <span class="article onetext">{{item.author}}<span class="line"></span>{{item.tag}}</span>
          <span class="detail onetext">{{item.introduce}}</span>
          <span class="popular onetext">{{item.time}}</span>
        </div>
      </div>
    </div>

    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
const API_PROXY = "https://bird.ioliu.cn/v1/?url=";
export default {
  name: "searchs",
  data() {
    return {
      //val用于获取input里面的值
      val: "",
      //seachbooks存储请求来的数据
      seachbooks: []
    };
  },

  methods: {

    //传入input的val值
    inpname(val) {
      // console.log("val==>",val)
      
      this.axios
        //模糊搜索
        .get(
          API_PROXY +
            "http://api.pingcc.cn/?xsname=" + this.val
        )
        .then(result => {
          // console.log("result ==> ", result);
          this.seachbooks = result.data;
          // console.log("this.seachbooks==>", this.seachbooks);
        })
        .catch(err => {
          // console.log("err ==> ", err);
        });
    },

    //点击跳转到显示页
    jump(o){
      this.$router.push(o)
    }
  }


};
</script>

<style lang="less" scoped>
.searchs {
  .search {
    height: 40px;
    background: #3b454d;
    text-align: center;
    line-height: 40px;
    color: #f5f5f5;
    font-size: 14px;
  }
  .box {
    position: fixed;
    top: 94px;
    left: 0;
    right: 0;
    bottom: 1.33333rem;
    background-color: #f2f2f2;
    padding: 10px 10px 0;
    overflow-y: auto;
    .lovebook {
      height: 2.85rem;
      overflow: hidden;
      background-color: rgba(219, 219, 219,.8);
      margin-bottom: 10px;
      .imgs {
        width: 80px;
        > img {
          width: 100%;
          line-height: 2.85rem;
          color: #645b5b;
        }
      }
      .books {
        width: calc(100% - 2.85rem - 20px);
        margin-left: 20px;
        margin-top: 14px;
        .titlt {
          display: block;
          font-size: 14px;
          font-weight: 800;
          margin-bottom: 2px;
        }
        .article {
          display: block;
          font-size: 12px;
          color: #645b5b;
          margin-bottom: 2px;
          .line{
              display:inline-block;
              width: 1px;
              height: 10px;
              background: #3b454d;
              margin: 0 5px;
          }
        }
        .detail{
            display: block;
            font-size: 12px;
            color: #645b5b;
            margin-bottom: 2px;
            font-weight: 300;
        }
        .popular{
            display: block;
            font-size: 13px;
            font-weight: 600;
            color:#645b5b; 
            .linet{
                display: inline-block;
                width: 1px;
                height: 11px;
                background-color: #645b5b;
                margin: 0 5px;
                font-size: 14px;
            }
        }
      }
    }
  }
}
</style>
<style lang="less">
.van-search {
  background: rgb(218, 217, 212) !important;
}
</style>